import React from 'react';
import 'antd/dist/antd.css';
import { Layout, Menu } from 'antd';
import { Link } from 'gatsby';
import logo from '../../images/logo.png';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Nav, Navbar } from 'react-bootstrap';

const { Header } = Layout;

const items = [
  {
    value: "Home",
    key: '',
  },
  {
    value: "Services",
    key: 'services',
  },
  {
    value: "Contact Us",
    key: 'contact-us',
  }
]

const PageHeader = (props: any) => (
  <>
    {false ? <Header style={{backgroundColor: "Red", color: "white"}}>Website Under Construction</Header> : <></>}
      <Navbar className="px-4" style={{backgroundColor: "#68a2bd"}} variant="dark" expand="lg">
          <Navbar.Brand>
            <Link to="/">
              <img height="40" src={logo} />
            </Link>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              {items.map((item, index) => (
                <Nav.Link>
                  <Link to={`/${item.key}`} style={{textDecoration: "none", color: item.key==props.selectedKey ? "#1f3745" : "white"}}>
                    {item.value}
                  </Link>
                </Nav.Link>
              ))}
            </Nav>
          </Navbar.Collapse>
      </Navbar>
  </>
);

export default PageHeader